import React from 'react';
import './styles/text-chapter.css';
import {WasteReasonsOptions} from '../Data/wasteReasonsData'
import Chart from 'react-apexcharts'

const WasteReasons = props => {
  return (
    <div label='chart holder' style={{backgroundColor: 'white', border: '2px black solid', paddingTop: '10px', marginTop: '15px'}}>
      <Chart 
            options={WasteReasonsOptions.options} 
            series={WasteReasonsOptions.series} 
            type="bar" 
            height={600} 
      />
    </div>
  );
}
export default WasteReasons;