export const textchapter1 = {
  tc1p1: "But surely food waste can just be composted...right? Wrong! About 55% of our food waste went to landfill in 2012 and this has a huge impact on the environment. 2012 CO2 emissions created through avoidable food waste totalled to 17 million tonnes, the equivalent of CO2 production by 1/4 of cars in the UK. This makes up approximately 1.7% of greenhouse gas production in the UK in 2012.",
  tc1p2: "Another big factor for most of the population is cost, the average person loses 200 a year just on avoidable waste. This is even more for single-person households at an average of 290. ",
  tc1p3: "As well as helping ourselves and the environment, edible food waste could be used to help others. Between April 2018 and March 2019, the UK's largest food bank charity, The Trussel Trust, handed out 1.6 million 3-day emergency food supplies. This number does not include regular food bank visits. UK avoidable food waste could be used to help overcome difficulties found by many UK families.",
  sources1: "WRAP, Household Food and Drink Waste in the United Kingdom 2012, November 2013, p10,35,36. - Various environmental informations and individual cost of food wasted",
  sources2: "Trussel Trust, End of year stats, found at https://www.trusselltrust.org/news-and-blog/latest-stats/end-year-stats/"

}

export const textchapter2 = {
  tc2p1: "Reducing food waste can be difficult, but there are many small household changes we can make to target avoidable food waste. ",
  tc2p2: "One way is to build a pantry of the necessities, the ingredients we can use to make the most dishes. This prevents infrequently used ingredients being pushed to the back of the cupboard and food waste from simply storing too much causing spoiling",
  tc2p3: "But with this method, we must prevent boredom. Food waste often arises when we choose another food due to boredom with what we already have. For the adventurous eater, this can easily be done by cooking different cuisines. Even with very similar ingredients, vastly different dishes can be created, stopping you from becoming bored. Remember - a quiche and a pancake have the same ingredients, but you wouldn't think of them as similar.",
  tc2p4: "But how do we do all that?"
}

