import React from 'react'
import {foodlabellingdataoptions} from '../Data/foodlabellingdata'
import Chart from 'react-apexcharts'

const foodlabelling = (props) => {
  return (
    <div label='chart holder' style={{backgroundColor: 'white', border: '2px black solid', paddingTop: '10px', marginTop: '15px'}}>
      <Chart
              options={foodlabellingdataoptions.options}
              series={foodlabellingdataoptions.series}
              type="bar"
              height={600}
      />

    </div>
  );
}

export default foodlabelling;