import React, { useRef } from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import Button from 'react-bootstrap/Button';
import Chapter from './Components/chapter';
import TextChapter from './Components/textChapter'
import Grid from './Components/grid';
import {textchapter1, textchapter2, chapter1text, chapter2text, chapter3text, chapter4text, chapter5text, chapter6text, chapter7text} from './Data/descriptions';
import FoodLabelling from './Components/foodlabelling'
