import React from 'react';
import './styles/home-page.css';
import HomeIcon from '../Icons/home-icon.png'
import Modal from 'react-bootstrap/Modal'

const HomePage = props => {





  return (
    <div className='home-page-comp'>
      <div className='page-title'>
        <p style={{paddingTop: '30px'}}> Love  </p>
        <img className='home-page-icon' src={HomeIcon}/>

        <p style={{paddingTop: '30px'}}> Food  </p>
      </div>
      <div className='introduction'>
        <p style={{fontSize:'34px', marginTop: '3px', marginBottomm: '15px'}}><i>Preventing food waste by loving what we eat</i></p>
        <p>
          UK households produce phenomenal amounts of food waste. 
          Modern day food accessibility has allowed for more choice than ever before, particularly in ready-meals and restaurant take-aways. The choice is extended even further with more availability of speciality ingredients in mainstream supermarkets. 
          However, these options can allow us to be lazy with cooking and preparing food we already have, or even waste food simply because we don't fancy whatever is already in our fridge.
          In other instances, food can be unintentionally wasted. Food may be stored incorrectly or the use by date not checked... or just pushed to the back of the cupboard after use in an unusual recipe. 
          This is where avoidable food waste arises. 
        </p>
        <br/>
        <p>
          In addition to avoidable food waste, households may also produce possibly avoidable food waste and unavoidable food waste. 
          Possibly avoidable includes foods which could have been consumed but are widely recognised as less preferable, potato skins or bread crusts, for example. 
          Unavoidable food waste describes completely inedible food components such as egg shells or meat bones.
        </p>
        <br/>
        <p>
          Most frequently, unavoidable food waste is produced from home cooking fresh ingredients, whereas ready-prepared meals are commonly completely edible. 
          However, this should not give the impression that ready-prepared meals are a good way to reduce food waste as packaging waste produced by food is a large contributor to landfill.
        </p>
      </div>
    </div>
  );
}
export default HomePage;

