import React from 'react';
import './styles/chapter.css';
import QuestionIcon from '@material-ui/icons/HelpOutline';
import Modal from 'react-bootstrap/Modal'


const Chapter = props => {

  const [documentationModalShow, setDocumentationModalShow] = React.useState(false);

  function DocumentationModal(props) {
    return (
      <Modal
        {...props}
        size="lg"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
             Using This Visualisation
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h5>Tips</h5>
          <p>
            {props.props.tips}
          </p>
          <h5>Sources</h5>
          <p>
            {props.props.sources}
          </p>
        </Modal.Body>
      </Modal>
    );
  }


  return (
    <div className='chapter-page-comp'>

      <div label='title' className='chapter-title'>
        {props.sectiontitle}
      </div>

      <div className='chapter-caption'>
        {props.description}
      </div>

      <div className='chapter-visualisation'>
        {props.visualisation} 
      </div>

      <div className='chapter-help-section'>
        <QuestionIcon style={{fontSize: 30}} onClick={() => setDocumentationModalShow(true)}/>
      </div>
      <DocumentationModal show={documentationModalShow} onHide={() => setDocumentationModalShow(false)} props={props}/>
    </div>
  );
}
export default Chapter;