import React from 'react';
import {FoodTypeAvoidabilityOptions} from '../Data/foodTypeAvoidabilityData'
import {FoodTypeAvoidabilityOptions2} from '../Data/foodTypeAvoidabilityData2'
import Chart from 'react-apexcharts'
import Button from 'react-bootstrap/Button'

const FoodTypeAvoidability = props => {

  const [orderBy, setOrderBy] = React.useState("Total Waste")
  const [dataSource, setDataSource] = React.useState(FoodTypeAvoidabilityOptions)

  const switchOrder = () => {
    if(orderBy === 'Total Waste'){
      setOrderBy("Unavoidable Waste")
      setDataSource(FoodTypeAvoidabilityOptions2)
    }
    else {
      setOrderBy("Total Waste")
      setDataSource(FoodTypeAvoidabilityOptions)
    }
  }

  return (
    <div label='chart holder' style={{backgroundColor: 'white', border: '2px black solid', paddingTop: '10px'}}>
      <Button variant='secondary' onClick={()=>switchOrder()}>Order by {orderBy}</Button>
      <Chart 
            options={dataSource.options} 
            series={dataSource.series} 
            type="bar" 
            height={600} 
      />
    </div>
  );
}
export default FoodTypeAvoidability;