import React from 'react';
import './styles/text-chapter.css';
import QuestionIcon from '@material-ui/icons/HelpOutline';
import Modal from 'react-bootstrap/Modal'

const TextChapter = props => {

  const [documentationModalShow, setDocumentationModalShow] = React.useState(false);

  function DocumentationModal(props) {
    return (
      <Modal
        {...props}
        size="lg"
        centered
      >
        <Modal.Header closeButton>
          <Modal.Title id="contained-modal-title-vcenter">
             Information Sources
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <h5>Sources</h5>
          <p>
            {props.props.sources}
          </p>
        </Modal.Body>
      </Modal>
    );
  }


  return (
    <div className='text-chapter-page-comp'>
      <div className='text-chapter-page-title'>
        {props.heading}
      </div>
      <br/>
      <div className='text-chapter-writing'>
        <p>{props.paragraph1}</p>
        <br/>
        <p>{props.paragraph2}</p>
        <br/>
        <p>{props.paragraph3}</p>
        <br/>
        <p>{props.paragraph4}</p>
        <br/>
        <p>{props.paragraph5}</p>
      </div>
      <div className='text-chapter-help-section'>
        <QuestionIcon style={{fontSize: 30}} onClick={() => setDocumentationModalShow(true)}/>
      </div>
      <DocumentationModal show={documentationModalShow} onHide={() => setDocumentationModalShow(false)} props={props}/>
    </div>
  );
}
export default TextChapter;