import React, {useState} from 'react';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import './styles/grid.css';
import AppleIcon from '../Icons/AppleIcon.png';
import PotatoIcon from '../Icons/PotatoIcon.png';
import OrangeIcon from '../Icons/OrangeIcon.png';
import BananaIcon from '../Icons/BananaIcon.png';
import BreadIcon from '../Icons/BreadIcon.png';
import HamIcon from '../Icons/HamIcon.png';
import MilkIcon from '../Icons/MilkIcon.png';
import TomatoIcon from '../Icons/TomatoIcon.png';
import CarrotIcon from '../Icons/CarrotIcon.png';
import ReactCardFlip from 'react-card-flip';
import RefreshIcon from '@material-ui/icons/Refresh';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import Tooltip from 'react-bootstrap/Tooltip';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger'

const Grid = (props) => {

  const [card1, setCard1] = useState(false)
  const [card2, setCard2] = useState(false)
  const [card3, setCard3] = useState(false)
  const [card4, setCard4] = useState(false)
  const [card5, setCard5] = useState(false)
  const [card6, setCard6] = useState(false)
  const [card7, setCard7] = useState(false)
  const [card8, setCard8] = useState(false)
  const [card9, setCard9] = useState(false)
  const [screen, setScreen] = useState(false)
  const [seeScreen, setSeeScreen] = useState(null)

  const reset = () => {
    setCard1(false)
    setCard2(false)
    setCard3(false)
    setCard4(false)
    setCard5(false)
    setCard6(false)
    setCard7(false)
    setCard8(false)
    setCard9(false)
    setScreen(false)
    setSeeScreen(true)
  }

  function renderTooltip(props) {
    return <Tooltip {...props}>Select a square to reveal a way that food could be used, click here to refresh</Tooltip>;
  }

  return (
    <>
      <div className='grid-holder' >  
        <Row className='grid-style' style={{display: seeScreen}}>

          <Col className='grid-style'>
            <ReactCardFlip isFlipped={card1} flipDirection="horizontal" flipSpeedBackToFront={1} flipSpeedFrontToBack={1}> 
              <div className='item-card' style={{backgroundColor: '#852818'}} onClick={() => setCard1(!card1)}>
                <img src={AppleIcon} className='icon-style'/>
                1,000,000 <br/>
                Apples
              </div>
              <div className='item-card2' style={{backgroundColor: '#852818'}} onClick={() => setCard1(!card1)}>
                Enough to feed every UK airport passenger 1 apple 
              </div>
            </ReactCardFlip>
          </Col>
          
          <Col className='grid-style'>
            <ReactCardFlip isFlipped={card2} flipDirection="horizontal" flipSpeedBackToFront={1} flipSpeedFrontToBack={1}> 
              <div className='item-card' style={{backgroundColor: '#C7860E'}} onClick={() => setCard2(!card2)}> 
                <img src={PotatoIcon} className='icon-style'/>
                4,400,000 <br/> Potatoes  
              </div>
              <div className='item-card2' style={{backgroundColor: '#C7860E'}} onClick={() => setCard2(!card2)}> 
                Enough to feed every university student in the UK 2 potatoes 
              </div>
            </ReactCardFlip>
          </Col>

          <Col className='grid-style'>
            <ReactCardFlip isFlipped={card3} flipDirection="horizontal" flipSpeedBackToFront={1} flipSpeedFrontToBack={1}> 
              <div className='item-card' style={{backgroundColor: '#852818'}} onClick={() => setCard3(!card3)}>
                <img src={OrangeIcon} className='icon-style'/>
                720,000 <br/> Oranges
              </div>
              <div className='item-card2' style={{backgroundColor: '#852818'}} onClick={() => setCard3(!card3)}> 
                Enough to give the entire population of Iceland a glass of orange juice each
              </div>
            </ReactCardFlip>
          </Col>

        </Row>
        <Row className='grid-style' style={{display: seeScreen}}>

          <Col className='grid-style'>
            <ReactCardFlip isFlipped={card4} flipDirection="horizontal" flipSpeedBackToFront={1} flipSpeedFrontToBack={1}> 
              <div className='item-card' style={{backgroundColor: '#C7860E'}} onClick={() => setCard4(!card4)}> 
                <img src={BananaIcon} className='icon-style'/>
                920,000 <br/> Bananas
              </div>
              <div className='item-card2' style={{backgroundColor: '#C7860E'}} onClick={() => setCard4(!card4)}> 
                Double enough to feed a banana to every UK child who goes to school hungry
              </div>
            </ReactCardFlip>
          </Col>

          <Col className='grid-style'>
            <ReactCardFlip isFlipped={card6} flipDirection="horizontal" flipSpeedBackToFront={1} flipSpeedFrontToBack={1}> 
              <div className='item-card' style={{backgroundColor: '#852818'}} onClick={() => setCard6(!card6)}>
                <img src={HamIcon} className='icon-style'/> 
                2,200,000 <br/> Slices Of Ham
              </div>
              <div className='item-card2' style={{backgroundColor: '#852818'}} onClick={() => setCard6(!card6)}>
                Enough to feed every pet dog in London 3 slices of ham
              </div>
            </ReactCardFlip>
          </Col>

          <Col className='grid-style'>
            <ReactCardFlip isFlipped={card5} flipDirection="horizontal" flipSpeedBackToFront={1} flipSpeedFrontToBack={1}> 
              <div className='item-card' style={{backgroundColor: '#C7860E'}} onClick={() => setCard5(!card5)}> 
                <img src={BreadIcon} className='icon-style'/>
                20,000,000 <br/> Slices Of Bread 
              </div>
              <div className='item-card2' style={{backgroundColor: '#C7860E'}} onClick={() => setCard5(!card5)}> 
                Enough to feed a sandwich to every school teacher and pupil in England 
              </div>
            </ReactCardFlip>
          </Col>

        </Row>
        <Row className='grid-style' style={{display: seeScreen}}>

          <Col className='grid-style'>
            <ReactCardFlip isFlipped={card8} flipDirection="horizontal" flipSpeedBackToFront={1} flipSpeedFrontToBack={1}> 
              <div className='item-card' style={{backgroundColor: '#852818'}} onClick={() => setCard8(!card8)}> 
                <img src={TomatoIcon} className='icon-style'/>
                1,200,000 <br/> Tomatoes 
              </div>
              <div className='item-card2' style={{backgroundColor: '#852818'}} onClick={() => setCard8(!card8)}> 
                Enough to feed every UK homeless person a bowl of tomato soup 
              </div>
            </ReactCardFlip>
          </Col>

          <Col className='grid-style'>
            <ReactCardFlip isFlipped={card7} flipDirection="horizontal" flipSpeedBackToFront={1} flipSpeedFrontToBack={1}> 
              <div className='item-card' style={{backgroundColor: '#C7860E'}} onClick={() => setCard7(!card7)}>  
                <img src={MilkIcon} className='icon-style'/>
                3,100,000 <br/> Glasses Of Milk
              </div>
              <div className='item-card2' style={{backgroundColor: '#C7860E'}} onClick={() => setCard7(!card7)}> 
                Enough to fill an Olympic sized swimming pool every 4 days
              </div>
            </ReactCardFlip>
          </Col>

          <Col className='grid-style'>
            <ReactCardFlip isFlipped={card9} flipDirection="horizontal" flipSpeedBackToFront={1} flipSpeedFrontToBack={1}> 
              <div className='item-card' onClick={() => setCard9(!card9)} style={{backgroundColor: '#852818'}}>
                <img src={CarrotIcon} className='icon-style'/>
                2,700,000 <br/> Carrots 
              </div>
              <div className='item-card2' onClick={() => setCard9(!card9)} style={{backgroundColor: '#852818'}}> 
                Enough to feed every UK pet rabbit 2 carrots
              </div>
            </ReactCardFlip>
          </Col>

        </Row>
      </div>


      <div label='icon-holder' className='icon-holder'>
        <OverlayTrigger placement="left" delay={{ show: 800, hide: 400 }} overlay={renderTooltip} >
          <RefreshIcon className='icon-style' onClick={()=>reset()}/>
        </OverlayTrigger>
      </div>
    </>
  );

}

export default Grid;